import React, { useState } from "react";
import PropTypes from "prop-types";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Avatar from "@vkontakte/vkui/dist/components/Avatar/Avatar";
import View from "@vkontakte/vkui/dist/components/View/View";
import ScreenSpinner from "@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner";

import "./Home.css";

function Home({
  id,
  fetchedUser,
  go,
  setActivePanel,
  data,
  db,
  findDocumentById,
}) {
  getNotes("1");

  //const [todos, setTodos] = useState([]);
  const [loading, setLoading] = useState(<ScreenSpinner size="large" />);
  const [notes1, setNotes1] = useState("");
  const [notes2, setNotes2] = useState("");
  const [notes3, setNotes3] = useState("");
  const [notes4, setNotes4] = useState("");

  function setTimer(value, f) {
    if (value) {
      f();
    } else {
      setTimeout(() => {
        setTimer(f);
      }, 100);
    }
  }

  if (fetchedUser) {
    findDocumentById(fetchedUser.id);
  } else {
    findDocumentById("0");
  }
  setTimeout(() => {
    console.log(data.docId);
  }, 3000);

  const NotesPreview = React.lazy(
    () =>
      new Promise((resolve) => {
        setTimer(notes1, resolve(import("./components/NotesPreview")));
      })
  );

  function getNotes({ id }) {
    db.collection("notes")
      .doc("1BkXPAiu9cP3DYWgAD7P")
      .get()
      .then(function (doc) {
        if (doc.exists) {
          if (doc.data().notes1) {
            setNotes1(doc.data().notes1);
            data.notes1 = doc.data().notes1;
          } else {
            setNotes1("Добавить");
          }
          if (doc.data().notes2) {
            setNotes2(doc.data().notes2);
            data.notes2 = doc.data().notes2;
          } else {
            setNotes2("Добавить");
          }
          if (doc.data().notes3) {
            setNotes3(doc.data().notes3);
            data.notes3 = doc.data().notes3;
          } else {
            setNotes3("Добавить");
          }
          if (doc.data().notes4) {
            setNotes4(doc.data().notes4);
            data.notes4 = doc.data().notes4;
          } else {
            setNotes4("Добавить");
          }
        } else {
          console.log("No such document!");
        }
        setLoading(null);
      });
  }

  function onNotesClick({ notesId }) {
    console.log("NotesId: " + notesId);
    setActivePanel("persik");
  }

  return (
    <View activePanel={id} popout={loading}>
      <Panel id={id}>
        <PanelHeader>Example</PanelHeader>
        {fetchedUser && (
          <Group title="User Data Fetched with VK Bridge">
            <Cell
              before={
                fetchedUser.photo_200 ? (
                  <Avatar src={fetchedUser.photo_200} />
                ) : null
              }
              description={
                fetchedUser.city && fetchedUser.city.title
                  ? fetchedUser.city.title
                  : ""
              }
            >
              {`${fetchedUser.first_name} ${fetchedUser.last_name}`}
            </Cell>
          </Group>
        )}

        {/* Notes */}
        <Div className="wrapper">
          <React.Suspense fallback={<div></div>}>
            <NotesPreview
              notesId={1}
              title={notes1.split("₪")[0]}
              onClick={onNotesClick}
            />
            <NotesPreview notesId={2} title={notes2} onClick={onNotesClick} />
            <NotesPreview notesId={3} title={notes3} onClick={onNotesClick} />
            <NotesPreview notesId={4} title={notes4} onClick={onNotesClick} />
          </React.Suspense>
        </Div>

        <Group title="Navigation Example">
          <Div>
            <Button size="xl" level="2" onClick={go} data-to="persik">
              Show me the Persik, please
            </Button>
          </Div>
        </Group>
      </Panel>
    </View>
  );
}

Home.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export default Home;
